    numberWithCommas = (number: number, decimals: number) => {
        let numbers: any = number?.toString().split('.'),
            wholeNumber: string | number = parseInt(numbers ? numbers[0] : 0);
        wholeNumber = isNaN(wholeNumber)
            ? 0
            : wholeNumber.toString().length < 10
                ? wholeNumber.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',')
                : wholeNumber.toExponential(2);
        return wholeNumber +
            '.' +
            ((numbers && numbers[1]?.substr(0, decimals || 2)) || '00'); // + '' + exp && (exp[1] ? 'e' + exp[1] : '')
    },
    week_of_month = (date: number) => {
        let prefixes = [1, 2, 3, 4, 5];
        return prefixes[0 | moment(date).date() / 7]
    },
    getDateTime = (ms: number) => {
        var date = new Date(ms),
            dateStr = date.toString().substr(0, 15);
        let now = Date.now(),
            firstDayOfWWeek = Date.parse(moment(new Date(date.setDate(date.getDate() - date.getDay()))).format('M/DD/YYYY')),
            lastDayOfWWeek = Date.parse(moment(new Date(date.setDate(date.getDate() - date.getDay() + 7))).format('M/DD/YYYY')),
            yesterday: any = new Date(new Date());
        yesterday = yesterday.setDate(yesterday.getDate() - 1)
        ms = ms || now;
        return {
            date: moment(ms).format('MMM D, YYYY'),//Mon May 03 2021
            monthDate: moment(ms).format('MMM D'),
            time: moment(ms).format('HH:mm'),
            month: moment(ms).format('MMM'),
            yesterday: moment(yesterday).format('M/DD/YYYY'),
            today: moment(now).format('M/DD/YYYY'),
            calendar: moment(ms).format('YYYY-MM-DD'),
            transDate: moment(ms).format('M/DD/YYYY'),
            year: moment(ms).format('YYYY'),
            dateStr: dateStr.substr(0, 10) + ' ' + dateStr.substr(13, 2),
            ms: date.getTime(),
            dbTime: dateStr.substr(0, 10) + ' ' + moment(ms).format('YYYY'),
            firstDayOfWWeek,
            lastDayOfWWeek,
            week: week_of_month(ms)
        }
    }
