import { useEffect, useState } from "react"
import Login from './login';
import queryString from 'query-string';
import Pay from './pay';
import { Api } from "../common/functions";
import ReconnectingWebSocket from 'reconnecting-websocket';
import urls from '../common/urls'
import { Container } from "@mui/system";
import { Box, Button, Card, Typography } from "@mui/material";
import { styled } from '@mui/material/styles';
import { ReportProblem } from '@mui/icons-material';
interface StateProps {
    isLoading: boolean,
    showPage: 'Login' | 'Pay',
    error: string | null,
    app: {
        icon: string,
        name: string,
        _id: string
    } | null,
    callback: string | null,
    access_token: null | string,
    user: any,
    currencies: any
}
const ws = new ReconnectingWebSocket(urls.socketUrl, [], ({
    constructor: WebSocket,
    connectionTimeout: 5000,  // in milliseconds
    reconnectInterval: 1000,
}) as any),
    sendData = (data: any) => ws.send(JSON.stringify(data)),
    OverviewWrapper = styled(Box)(({ theme }) => `
    img {
      width: 70px;
      height: 70px;
      display: block;
      margin-bottom: 20px;
    }

    .iconProblem {
        color:red
    };
    
    .errorMessage {
        font-size:0.6em;
    };

    .oops {
        margin-bottom:${theme.spacing(2)};
        color:red
    }
    .closeBtn {
        text-transform: none;
        margin:${theme.spacing(2, 0)};
        color:#000;
        text-decoration:underline;
        font-size:0.53em;
    }

    `)
const Index = () => {
    const [state, setState] = useState<StateProps>({
        isLoading: true,
        showPage: 'Login',
        error: null,
        app: null,
        callback: null,
        access_token: null,
        user: null,
        currencies: null
    }),
        { isLoading, showPage, error, app, access_token, user, currencies } = state,
        params = queryString.parse(window.location.search),
        onlyLogin = window.location.pathname === '/login',
        socket = () => {
            ws.onopen = (e) => {
                // sendData({
                //     action: 'developerOnline'
                // });
            }
            ws.onerror = (e) => {
                console.log('error')
            }
            ws.onmessage = (e) => {
                let data = JSON.parse(e.data)
                const { action, user, currencies } = data
                if (action === 'userLogged') {
                    if (onlyLogin)
                        window.close()
                    else
                        setState({
                            ...state,
                            isLoading: false,
                            showPage: 'Pay',
                            user,
                            currencies
                        })
                }
            }
            ws.onclose = (e) => {
                console.log('socket closed')
            }
        },
        decrypt = () => {
            Api(
                {
                    action: 'decrypt_app_payment',
                    encrypted: true,
                    data: params.data
                }).then((resp: any) => {
                    const {
                        access_token,
                        callback,
                        error,
                        transaction,
                        app
                    } = resp
                    if (error)
                        setState({
                            ...state,
                            isLoading: false,
                            error: resp.error
                        });
                    else {
                        if (onlyLogin)
                            setState({
                                ...state,
                                app,
                                isLoading: false,
                                callback,
                                access_token
                            })
                        else {
                            let Transaction = {
                                checkOutInfo: transaction,
                                app,
                                recepient: resp.recepient,
                                access_token
                            };
                            setState({
                                ...state,
                                app,
                                isLoading: false,
                                access_token
                            });
                            localStorage.setItem('benkiko_transaction', JSON.stringify(Transaction))
                        }
                        sendData({
                            action: 'developerOnline',
                            socketId: params.socketId || undefined
                        })
                    }
                }).catch(err => {
                    setState({
                        ...state,
                        isLoading: false,
                        error: "An error occured please try again later."
                    })
                })
        }
    useEffect(() => {
        decrypt()
        socket()
    }, [])
    return (
        error ?
            <OverviewWrapper>
                <Container maxWidth="xs">
                    <Box display="flex" flexDirection={'column'} justifyContent="center" py={5} alignItems="center">
                        <img
                            alt={''}
                            src={'https://benkiko.s3.us-east-2.amazonaws.com/logo.png'}
                        />
                        <Typography component="h1" variant="h6">Benkiko</Typography>
                    </Box>
                    <Card sx={{ p: 2, pl: 4, pr: 4, borderRadius: 3 }}>
                        <ReportProblem classes={{ root: 'iconProblem' }} />
                        <Typography
                            classes={{
                                root: 'oops'
                            }}
                        >Ooops!</Typography>
                        <Typography
                            classes={{
                                root: 'errorMessage'
                            }}
                        >{error}</Typography>
                        <Button
                            classes={{ root: 'closeBtn' }}
                            onClick={() => window.close()}
                        >Close</Button>
                    </Card>
                </Container>
            </OverviewWrapper> :
            isLoading ?
                <div className="spinner-container">
                    <div className="loading-spinner">
                    </div>
                </div> :
                showPage === 'Pay' ?
                    <Pay
                        backToLogin={() => setState({
                            ...state,
                            showPage: 'Login'
                        })}
                        socketId={params.socketId as string}
                        user={user}
                        currencies={currencies}
                    /> :
                    <Login
                        socketId={params.socketId as string}
                        appId={app?._id}
                        access_token={access_token}
                        onlyLogin={onlyLogin}
                    />
    )
}

export default Index