import {
    Box,
    Container,
    Card,
    Typography,
    TextField,
    Grid,
    InputAdornment,
    IconButton,
    OutlinedInput,
    InputLabel,
    FormControl
} from '@mui/material';
import { QRCodeSVG } from 'qrcode.react';
import { styled } from '@mui/material/styles';
import { FC, useEffect, useRef, useState } from 'react';
import { LoadingButton } from '@mui/lab';
import { Api } from '../common/functions';
import { Visibility, VisibilityOff } from '@mui/icons-material';
interface StatePRops {
    emailError: null | string,
    passwordError: null | string,
    loading: boolean,
    showPass: boolean,


}
interface Props {
    appId?: string,
    socketId: null | string
    access_token: null | string
    onlyLogin: boolean,
}

const OverviewWrapper = styled(Box)(({ theme }) => `
    img {
      width: 70px;
      height: 70px;
      display: block;
      margin-bottom: 20px;

    }
    .hrView{
        display: flex;
        flex-direction: row;
        margin-top: ${theme.spacing(2)};
        align-items:center
    }
    .hr {
        width: 50%;
        height:0.5px
    }
    .orTxt {
      padding-left:10px;
      padding-right:10px;
      font-size:20px
    }
    .action_btn {
      background-color:#DEC10C;
      color:#000
    }
    .action_btn:hover {
      background-color:#F7F3DB;
    }
    .signInBtn{
        text-transform:none
    }
`
),
    CssTextField = styled(TextField)({
        '& label.Mui-focused': {
            color: '#000',
        },
        '& .MuiInput-underline:after': {
            borderBottomColor: '#000',
        },
        '& .MuiOutlinedInput-root': {
            '& fieldset': {
                borderColor: '#000',
            },
            '&:hover fieldset': {
                borderColor: 'B59504',
            },
            '&.Mui-focused fieldset': {
                borderColor: '#B59504',
            },
        },
    }),
    CssPin = styled(FormControl)({
        '& label.Mui-focused': {
            color: '#000',
        },
        '& .MuiInput-underline:after': {
            borderBottomColor: '#000',
        },
        '& .MuiOutlinedInput-root': {
            '& fieldset': {
                borderColor: '#000',
            },
            '&:hover fieldset': {
                borderColor: 'B59504',
            },
            '&.Mui-focused fieldset': {
                borderColor: '#B59504',
            },
        },
    }),


    Login: FC<Props> = ({
        appId,
        socketId,
        access_token,
        onlyLogin
    }) => {
        const [state, setState] = useState<StatePRops>({
            emailError: null,
            loading: false,
            passwordError: null,
            showPass: false
        }),
            componentIsMounted = useRef(true),
            { emailError, loading, passwordError, showPass } = state,
            handleSubmit = (e: any) => {
                e.preventDefault()
                let obj = { Pin: '', Email: '' },
                    data = new FormData(e.target)
                data.forEach((v, k) => (obj as any)[k] = v);

                setState({
                    ...state,
                    loading: true
                })
                // onChangePin(obj.Pin)
                Api({
                    action: 'login_user_by_sdk',
                    sdk: true,
                    ...obj,
                    socketId,
                    jwtToken: access_token,
                    onlyLogin
                }).then((resp: any) => {
                    if (resp.message)
                        setState({
                            ...state,
                            loading: false,
                            emailError: resp.message
                        })
                }).catch(e => {
                    setState({
                        ...state,
                        loading: false
                    })
                    console.log(e)
                })
            }


        useEffect(() => {
            return () => {
                componentIsMounted.current = false;
            }

        }, [])
        return (
            <OverviewWrapper>
                <Container maxWidth="xs">
                    <Box display="flex" flexDirection={'column'} justifyContent="center" py={5} alignItems="center">
                        <img
                            alt={''}
                            src={'https://benkiko.s3.us-east-2.amazonaws.com/logo.png'}
                        />
                        <Typography component="h1" variant="h5">Benkiko</Typography>
                    </Box>
                    <Card sx={{ p: 2, pl: 4, pr: 4, borderRadius: 3 }}>
                        <Box component="form"
                            onSubmit={handleSubmit}
                            method={'post'}
                            autoComplete={'off'}
                            sx={{ mt: 1 }}>
                            <CssTextField
                                margin="normal"
                                required
                                fullWidth
                                label="Email Address or Username"
                                name="Email"
                                autoComplete={'off'}
                                variant='outlined'
                                error={Boolean(emailError?.trim())}
                                helperText={emailError}
                                color={'info'}
                                onFocus={() => setState({
                                    ...state,
                                    emailError: null
                                })}
                            
                            />
                            <CssPin
                                fullWidth
                                variant="outlined"
                                margin='normal'
                                required

                            >
                                <InputLabel htmlFor="outlined-adornment-password">Pin</InputLabel>
                                <OutlinedInput
                                    name='Pin'
                                    id="outlined-adornment-password"
                                    type={showPass ? 'text' : 'password'}
                                    inputProps={{
                                        maxLength: 6
                                    }}
                                    autoComplete={'off'}
                                    autoSave={'none'}
                                    onFocus={() => setState({
                                        ...state,
                                        emailError: null
                                    })}
                                    endAdornment={
                                        <InputAdornment position="end">
                                            <IconButton
                                                aria-label="toggle password visibility"
                                                onClick={() => setState({
                                                    ...state,
                                                    showPass: !showPass
                                                })}
                                                edge="end"
                                            >
                                                {showPass ? <VisibilityOff /> : <Visibility />}
                                            </IconButton>
                                        </InputAdornment>
                                    }
                                    label="Pin"
                                />
                            </CssPin>
                            <LoadingButton
                                type="submit"
                                fullWidth
                                variant="contained"
                                className={'action_btn'}
                                loading={loading}
                                disabled={loading}
                                classes={{
                                    root: 'signInBtn'
                                }}
                            >
                                Sign In
                            </LoadingButton>
                            {
                                socketId ?
                                    <Grid  >
                                        <div
                                            className={'hrView'}>
                                            <hr className={'hr'} />
                                            <div className='orTxt'>Or</div>
                                            <hr className={'hr'} />
                                        </div>
                                        <br />
                                        <Grid
                                            display={'flex'}
                                            alignItems={'center'}
                                            flexDirection={'column'}
                                        >

                                            <Grid mb={2}>
                                                <Typography>Scan Qr With Benkiko App </Typography>
                                            </Grid>
                                            <QRCodeSVG
                                                value={`https://developer.benkiko.io?socketId=${socketId}&appId=${appId}&userSocket&onlyLogin=${onlyLogin}`}
                                            />
                                        </Grid>
                                    </Grid> :
                                    null
                            }
                        </Box>
                    </Card>
                </Container>
            </OverviewWrapper>
        );
    }

export default Login;
