import { Container } from "@mui/system"
import { styled } from '@mui/material/styles';
import { Box, Button, Card, Typography } from "@mui/material";
import { FC, Fragment, useEffect, useState } from "react";
import { Image, Info } from "@mui/icons-material";
import { missingIcon, successImage } from "../common/images";
import { Api, getDateTime, numberWithCommas } from "../common/functions";
import { LoadingButton } from "@mui/lab";
import invoke from 'react-native-webview-invoke/browser';

interface StateProps {
    loading: boolean,
    error: null | string,
    success: boolean
}
interface Props {
    backToLogin: () => void
    socketId: null | string,
    user: any,
    currencies: any
}
const OverviewWrapper = styled(Box)(({ theme }) => `
    img {
        width: 30px;
        height: 30px;
        display: block;
        margin-bottom: 20px;

    };

    .payTxt {
        font-size:16px
    }

    .price {
        font-size:15px;
        margin-top:${theme.spacing(1)}
    }
    
    .amountValue {
        color:red
    }

    .title {
        font-size:0.6em;
        font-weight:bold
    }

    .desc {
        font-size:0.51em;
    }

    .payBtn {
        text-transform:none;
        background-color:#DEC10C;
        color:#000;
         margin-top:${theme.spacing(5)};
         margin-bottom:${theme.spacing(4)}
    }

    .payBtn:hover {
        background-color:#F7F3DB;
      }

      .closeBtn {
        text-transform: none;
        color:#000;
        font-size:0.53em;
        text-decoration:underline
    }

    .error {
        font-size:0.60em;
        dispaly:flex;
        justifycontent:center;
        margin:${theme.spacing(2, 0, 0, 0)}
    }
 
`),
    TitleDescription = styled(Box)(({ theme }) => `
    display:flex;
    flex-direction:column;
    align-items:flex-start;
    border-bottom: 0.5px solid #C5C5C5;

    .escSub {
        font-size:0.6em;
        margin-bottom:${theme.spacing(1)}
    }
`),
    Header = styled(Box)(({ theme }) => `
        display:flex;
        flex-direction:column;
        align-items:center;

        img{
            width: 70px;
            height: 70px;
            margin-bottom: 0px;
            border-radius:50%
        };
        .appName {
            font-size:16px;
            margin-top:10px;
        }

       
`),
    AmountBox = styled(Box)(({ theme }) => `
             display:flex;
             justify-content:space-between;
            .value{
                font-size:0.55em
            }
    `),
    SuccessBox = styled(Box)(({ theme }) => `
         margin:${theme.spacing(4, 0)};
         display:flex;
         flex-direction:column;
         align-items:center;

        .successMsg {
            margin:${theme.spacing(1)};
            font-size:0.6em
        }

    `),
    Pay: FC<Props> = ({
        backToLogin,
        socketId,
        user,
        currencies
    }) => {
        const transaction = JSON.parse(localStorage.getItem('benkiko_transaction') as string),
            [state, setState] = useState<StateProps>({
                loading: false,
                error: null,
                success: false
            }),
            { loading, error, success } = state,
            {
                checkOutInfo,
                app,
                recepient,
                access_token
            } = transaction,
            PayFn = () => {
                setState({ ...state, loading: true })
                let date = Date.now(),
                    {
                        currency,
                        convAmnt,
                        fee,
                        redirectUrl
                    } = checkOutInfo,
                    sendObj = {
                        ...checkOutInfo,
                        action: 'new_payment',
                        socketId,
                        benkikoSdk: true,
                        userId: user._id,
                        jwtToken: access_token,
                        paymentInfo: {
                            ToId: recepient?._id,
                            ToAddress: recepient?.PublicKey,
                            amount: convAmnt,
                            asset: 'USDB',
                            currency: currency,
                            symbol: currencies[currency].symbol,
                            FromId: user._id,
                            FromAddress: user.PublicKey,
                            Username: user.Username,
                            Image: user.Image,
                            fee: parseFloat(fee),
                            userId: user._id,
                            date,
                            week: getDateTime(date).week,
                            year: getDateTime(date).year,
                            month: getDateTime(date).month,
                            createdAt: new Date(date).toString(),
                            appId: app?._id
                        }
                    }
                Api(sendObj).then((resp: any) => {
                    if (resp.error)
                        setState({ ...state, loading: false, error: resp.error });
                    else {
                        const paymentResponse = invoke.bind('paymentResponse');
                        paymentResponse().then(res => alert(res)).catch(err => alert(err));
                        setState({
                            ...state,
                            loading: false,
                            success: true
                        });
                        if (redirectUrl)
                            window.location.href = redirectUrl
                        else
                            setTimeout(() => {
                                window.close();
                            }, 2000)
                    }
                }).catch(err => {
                    setState({ ...state, error, loading: false })
                })
            }
        useEffect(() => {
            // localStorage.removeItem('benkiko_transaction')
        }, [])
        return (
            <OverviewWrapper>
                <Container maxWidth="xs">
                    <Box display="flex" flexDirection={'column'} justifyContent="center" py={5} alignItems="center">
                        <img
                            alt={''}
                            src={'https://benkiko.s3.us-east-2.amazonaws.com/logo.png'}
                        />
                        <Typography
                            classes={{
                                root: 'payTxt'
                            }}
                        >Pay With Benkiko</Typography>
                    </Box>
                    <Card sx={{ p: 2, pl: 4, pr: 4, mb: 10, borderRadius: 1, minWidth: 270 }}>
                        <Header>
                            <img
                                src={app?.icon || missingIcon}
                                alt="Benkiko"
                            />
                            <Typography classes={{ root: 'appName' }}  >{app.name}</Typography>
                        </Header>

                        <Typography
                            classes={{ root: 'price' }}
                        >
                            {checkOutInfo.currency}  <span
                                style={{ fontSize: 22 }}>{numberWithCommas(checkOutInfo.amount, 2)}</span>
                        </Typography>
                        {
                            !success ?
                                <Fragment>
                                    <TitleDescription
                                        mt={1}
                                        pt={1}
                                        pb={2}
                                        classes={{
                                            root: 'descTitle'
                                        }}>
                                        {
                                            checkOutInfo.escrow || checkOutInfo.subscription ?
                                                <Typography classes={{
                                                    root: 'escSub'
                                                }}>
                                                    <Info style={{
                                                        color: 'red',
                                                        fontSize: 13
                                                    }} />  {
                                                        checkOutInfo.escrow ? 'Escrow' : 'Subscription'
                                                    }
                                                </Typography> : null
                                        }
                                        <Typography
                                            classes={{
                                                root: 'title'
                                            }}
                                            mb={0.2}>{checkOutInfo.title}</Typography>
                                        <Typography
                                            classes={{
                                                root: 'desc'
                                            }}
                                        >{checkOutInfo.description}</Typography>
                                    </TitleDescription>
                                    <AmountBox pt={1}>
                                        <Typography classes={{
                                            root: 'value'
                                        }}>Fee</Typography>
                                        <Typography classes={{
                                            root: 'value'
                                        }}>{checkOutInfo.currency} {numberWithCommas(checkOutInfo.fee, 2)}</Typography>
                                    </AmountBox>
                                    <AmountBox>
                                        <Typography classes={{
                                            root: 'value'
                                        }}>Total</Typography>
                                        <Typography classes={{
                                            root: 'value'
                                        }}>{checkOutInfo.currency} {numberWithCommas((+checkOutInfo.amount + checkOutInfo.fee), 2)}</Typography>
                                    </AmountBox>
                                    {error ? <Typography
                                        classes={{
                                            root: 'error'
                                        }}
                                    >
                                        <Info style={{
                                            color: 'red',
                                            fontSize: 13
                                        }} />  {error}
                                    </Typography> : null}
                                    <LoadingButton
                                        fullWidth
                                        variant="contained"
                                        loading={loading}
                                        disabled={loading}
                                        classes={{
                                            root: 'payBtn'
                                        }}
                                        onClick={PayFn}
                                    >
                                        <span style={{
                                            opacity: loading ? 0 : 1
                                        }}>Pay</span>
                                    </LoadingButton>
                                </Fragment> :
                                null
                        }
                        {
                            success ?
                                <SuccessBox>
                                    <Typography
                                        classes={{
                                            root: 'successMsg'
                                        }}
                                    >Payment made successfully</Typography>
                                    <img src={successImage} />
                                </SuccessBox> :
                                null
                        }
                        <Button
                            classes={{ root: 'closeBtn' }}
                            onClick={() => window.close()}
                        >{!success ? 'Cancel' : 'Close'}</Button>
                        {
                            !success ?
                                <Box>
                                    <Button
                                        classes={{ root: 'closeBtn' }}
                                        onClick={backToLogin}
                                    >Back to Login</Button>
                                </Box> :
                                null
                        }

                    </Card>
                </Container>
            </OverviewWrapper>
        )
    }

export default Pay